
import { defineComponent, ref, computed } from 'vue'
import scrollChild from './scrollChild.vue'

export default defineComponent({
  name: 'Scroll',
  components: {
    scrollChild
  },
  props: {
    height: {
      default: () => { return 18 },
      type: Number
    },
    data: {
      default: () => { return [] },
      type: Array
    },
    ease: {
      default: () => { return false },
      type: Boolean
    },
    speed: {
      default: () => { return 1000 },
      type: Number
    },
    holdUp: {
      default: () => { return 1000 },
      type: Number
    },
    showLine: {
      default: () => { return 1 },
      type: Number
    }
  },
  setup (props) {
    const isHover = ref(false)

    const outerHeight = computed(() => {
      return { height: `${props.showLine * props.height}px`, overflow: 'hidden' }
    })

    const handleMouEn = () => {
      isHover.value = true
    }

    const handleMouOut = () => {
      isHover.value = false
    }

    return { isHover, handleMouEn, handleMouOut, outerHeight }
  }
})
